a,img,button,input,textarea{-webkit-tap-highlight-color:rgba(255,255,255,0);}

.box{
  -webkit-user-select: none;/*禁用手机浏览器的用户选择功能 */  
  -moz-user-select: none; 
  margin: 15px 0;
  text-align:center;
  
  .inputbox{
    border:1px solid #e8e8e8;
    border-radius:4px;
    width:48px;
    height:48px;
    margin-right: 10px;
    text-align: center;
    outline:none;
    -webkit-appearance: none;
  }

  .inputBtn{
      border:1px solid #e8e8e8;
    border-radius:4px;
    width:48px;
    height:48px;
    margin-right: 10px;
    text-align: center;
    outline:none;
    -webkit-appearance: none;
  }

  .select{
    border:1px solid #ff7739;
  }

}
 .ver-btn{
    background:#cecece;
    border-radius:2px;
    width:125px;
    height:30px;
    line-height: 30px;
    margin: 5px auto;
    color: #ffffff;
    font-family:'PingFangSC-Regular';
    font-size:14px;
    text-align: center;
  }
  .click_btn{
    background:#ff7739;
  }









